<template>
  <div class="barCharts">
    <div id="barCharts" ref="barCharts"></div>
  </div>
</template>

<script>
  import * as echarts from 'echarts';
  //这是绘制图表的参数定义
  // var _this = this;
  let optionTemp =
    {
      tooltip: {
        trigger: 'axis',
        backgroundColor: 'rgba(42, 92, 176, .6)',
        borderColor: "rgba(0, 255, 239, 1.0)",
        textStyle: {
          color: '#00FFEF'
        },
        axisPointer: {
          type: 'cross',
          crossStyle: {
            color: '#999'
          }
        }
      },
      grid: {
        left: '0',
        top: '20%',
        right: '4%',
        bottom: '15',
        containLabel: true
      },
      legend: {
        show:true,
        data: ['修订与新增制度完成情况','废止制度完成情况'],
        x:'right',      //可设定图例在左、右、居中
        y:'top',     //可设定图例在上、下、居中
        padding:[0,50,0,0],   //可设定图例[距上方距离，距右方距离，距下方距离，距左方距离
        textStyle: {color: '#fff'},

      },
      xAxis: [
        {
          type: 'category',
          data: [0],
          axisPointer: {
            type: 'shadow'
          },
          axisTick: { //x轴刻度线
            show: false,
          },
          axisLine: { //x轴
            show: false,
            // lineStyle:{
            //   color:'#00FF00',
            // }
          },
          axisLabel: { //设置x轴的字
            show: true,
            interval:0,//使x轴横坐标全部显示
            // rotate:50,//旋转
            textStyle: {//x轴字体样式
              color: "#333333",
              margin: 15,
              fontWeight: 'bold',
            }
          },
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: '单位：%',
          min: 0,
          max: 100,
          interval: 20,
          left:'0',
          nameTextStyle:{
            color:"#66B9FF",
        },
          axisLabel: { //设置y轴的字
            show: true,
            // formatter: '{value} ml',
            color:'#ff8000',
            interval: 0,//使y轴横坐标全部显示
            formatter:'{value}',
            textStyle: {//y轴字体样式
              color: "#333333",
              margin: 15,
            }
          },
          splitLine: {//网格线样式
            show: true,
            lineStyle:{
              color: '#0065B3',
              width: 1,
              type: 'solid'
            }
          },
          axisTick: { //y轴刻度线
            show: false,
          },
          axisLine: { //y轴
            show: false,
          },
        },
        // {
        //   type: 'value',
        //   name: '单位：份数',
        //   min: 0,
        //   max: 100,
        //   interval: 20,
        //   left:'0',
        //   nameTextStyle:{
        //     color:"#66B9FF",
        // },
        //   axisLabel: { //设置y轴的字
        //     show: true,
        //     color:'#ff8000',
        //     interval: 0,//使y轴横坐标全部显示
        //     textStyle: {//y轴字体样式
        //       color: "#66B9FF",
        //       margin: 15,
        //     }
        //   },
        //   splitLine: {//网格线样式
        //     show: true,
        //     lineStyle:{
        //       color: '#0065B3',
        //       width: 1,
        //       type: 'solid'
        //     }
        //   },
        // },
      ],
      series: [
        {
          name: '修订与新增制度完成情况',
          type: 'bar',
          barWidth : 30,//柱图宽度
          data: [8,12,17,20,25,30,35,40,45,60],
          itemStyle: {
            normal: {
                label:{
                   show: true,
                   position: 'top',
                   textStyle: {
                   color: '#08a4fa'
                  }
                },
              color:"#0490EC"
            }
          },
        },
        {
          name: '废止制度完成情况',
          type: 'bar',
          barWidth : 30,//柱图宽度
          data: [10,20,30,40,50,60,70,80,90,93],
          itemStyle: {
            normal: {
                label:{
                   show: true,
                   position: 'top',
                   textStyle: {
                   color: '#08a4fa'
                  }
                },
              color:"rgb(46,216,154)"
            }
          },
        }
      ]
    }


  export default {
    name: "barCharts",
    props: {
      index:{
        type:Number,
        default:()=>{
          return null
        }
      },
      xData:{
        /*** * xData:['一月',二月]*/
        type:Array,
        default:()=>{
          return []
        }
      },
      seriesData: {
        type: Array,
        default: () => {
          return []
        }
      },
      //  决定x轴和y轴显示(单位)
      _zb: {
        /** :['单位','']*/
        type: Array,
        default: () => {
          return ['单位', '']
        },

        //echarts 样式
        legendStyle:{
          type:Object,
          default:()=>{
            return {color: '#333333'}
          }
        },
        xStyle:{
          type:Object,
          default:()=>{
            return {
              color: "#333333",
              margin: 15,
              fontWeight: 'bold',
            }
          }
        },
        yStyle:{
          type:Object,
          default:()=>{
            return {
              color: "#333333",
              margin: 15,
            }
          }
        }
      },

      chartsData:{
        type:Array,
        default:()=>{
            return []
          }
      }

    },
    watch: {
      '$root.themeHomeChange'() {
        this.init();
      }
    },
    data() {
      return {
        month:'',
        option: optionTemp,
      }


    },
    mounted() {
      this.init();
    },
    methods: {
      setTheme(){
        if (this.$root.themeHomeChange==='2'){  //深色背景
          this.option.legend.textStyle = {color: '#ffffff'}
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#ffffff",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].axisLabel.textStyle = {
            color: "#66B9FF",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].splitLine.lineStyle.color = '#0065b3'
        }else if(this.$root.themeHomeChange==='1'){ //浅色背景
          this.option.legend.textStyle = {color: '#333333'}
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 16,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 16,
          }
          this.option.yAxis[0].splitLine.lineStyle.color = '#ccc'
        }
      },
      dataSet(){
        this.option.yAxis[0].name = this._zb[0]
        this.option.xAxis[0].name = this._zb[1]
        this.option.xAxis[0].data = this.xData
        if (this.seriesData.length>0){
          this.option.legend.data = []
          this.seriesData.forEach(item=>{
            this.option.legend.data.push(item.name)
          })
          this.option.series = this.seriesData
        }

        if(this.chartsData.length > 0){
          var _this = this
          this.option.tooltip.formatter = function(params){
            const data = {}
            _this.chartsData.forEach((i)=>{
               if(params[0].name == i.month){
                 data.month = i.month
                 data.plain = i.plain
                 data.reality = i.reality
                 data.systemPlain = i.systemPlain
                 data.systemReality = i.systemReality
               }
            })
            return `${params[0].name}<br/>
              修订与新增制度完成情况：<br/>
              计划修编${data.plain}份，实际完成${data.reality}份，累计完成率${params[0]? params[0].value : '0'}%。<br/>
              <br/>废止制度完成情况：<br/>
              计划修编${data.systemPlain}份，实际完成${data.systemReality}份，累计完成率${params[1]? params[1].value : '0'}%
              `;
          }
        }

        //样式
        this.setTheme()
      },
      init() {
        this.dataSet()
        let mychart = echarts.init(this.$refs.barCharts, );
        //将会绘制图表的参数加到图表中

        mychart.setOption(this.option);
        let $this = this;
        mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index)
        });
        window.addEventListener("resize", ()=>{
          mychart.resize()
        });

      },
    }

  }
</script>

<style scoped lang="scss">
.barCharts {
  height: 100%;
  #barCharts {
    height: 100%;
  }
}
</style>